import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: ""
  },
  mutations: {
    loginAfter (state, payload) {
      state.token = payload
    },
    logout (state) {
      state.token = ""
    }
  }
})


